section{z-index:1;display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);padding:var(--row-margin) 0;background:var(--background-color)}section::before{content:"";position:absolute;z-index:-1;top:0;left:50%;width:100vw;height:100%;background:var(--background-color);transform:translateX(-50%)}section:last-child{margin-bottom:var(--footer)}@media screen and (max-width: 1023px){section{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 767px){section{grid-template-columns:repeat(2, 1fr)}}