footer{position:absolute;z-index:99;display:flex;align-items:flex-start;justify-content:center;bottom:0;left:0;width:100%;height:var(--footer);background:none}footer .footer{z-index:1;display:grid;grid-template-columns:repeat(4, 1fr);-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);width:100%;max-width:var(--body);padding:var(--row-margin) var(--margin)}@media screen and (max-width: 1023px){footer .footer{grid-template-columns:repeat(2, 1fr)}footer .footer :nth-child(2),footer .footer :nth-child(3){display:none}}