.numeric{display:flex;flex-direction:column;align-items:flex-end;justify-content:flex-end;gap:calc(var(--row-margin)*.5)}.numeric p{text-wrap:nowrap}.numeric div{display:flex;align-items:flex-end;gap:.125em}.numeric div span{font-weight:500;line-height:1}.numeric div span:first-child{font-size:calc(var(--font-size)*5);letter-spacing:-0.025em}.numeric div span:last-child{margin-bottom:.25em;font-size:calc(var(--font-size)*2);font-weight:700}.numeric.-large span:first-child,.numeric.-large span:last-child{margin:unset;font-size:calc(var(--font-size)*6);font-weight:500;letter-spacing:-0.05em}@media screen and (max-width: 767px){.numeric:has(p){flex-direction:row;align-items:center;justify-content:space-between}.numeric.-large span:first-child,.numeric.-large span:last-child{font-size:calc(var(--font-size)*5);letter-spacing:-0.025em}}