.img{overflow:hidden}.img div{z-index:1;position:absolute;display:flex;align-items:center;justify-content:center;top:50%;left:50%;transform:translate(-50%, -50%);overflow:hidden;pointer-events:none}.img div span{color:var(--white-color);transform:translateY(100%);font-size:var(--caption-size);font-weight:var(--caption-weight);line-height:var(--caption-height);letter-spacing:var(--caption-spacing);text-transform:uppercase;text-wrap:nowrap;opacity:0}.img div span,.img img{display:block;transition:all .6s var(--ease)}.-hover .img div span{transform:translateY(0);opacity:1}.-hover .img img{transform:scale(108%);filter:brightness(72%)}