.row{display:grid;align-items:flex-start;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);width:100%;padding:var(--row-margin) 0;overflow:hidden}.row:last-child,.row.-fit{background-image:unset}.row.-fit{padding:unset}@media screen and (max-width: 1023px){.row.-fit{grid-template-columns:repeat(2, 1fr)}.row.-fit :nth-child(2),.row.-fit :nth-child(3){display:none}}.row.-flow{display:block;-moz-column-count:4;column-count:4;-moz-column-fill:balance;column-fill:balance;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--grid-gap)}.row.-flow p:last-child{margin-bottom:unset}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 767px){.row.-flow{-moz-column-count:auto;column-count:auto}}.row .cell{display:flex;flex-direction:column;align-items:flex-start;gap:var(--grid-gap)}.row .-spn100{grid-column:1/-1}.row .-spn50{grid-column:span 2}@media screen and (max-width: 767px){.row .-spn50{grid-column:span 1}}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 767px){.row{grid-template-columns:100%}}section .row{opacity:0;background-repeat:no-repeat;background-image:linear-gradient(var(--border-color), var(--border-color)),linear-gradient(var(--border-color), var(--border-color));background-size:50% 1px,50% 1px;background-position:-300% 100%,-210% 100%}section .row.-on{animation:rowSlideUp 1s var(--ease) forwards}@keyframes rowSlideUp{from{background-position:-300% 100%,-210% 100%;transform:translateY(2rem);opacity:0}to{background-position:0 100%,100% 100%;transform:translateY(0);opacity:1}}