body:has(main#home) .fab.-left{display:none}body:has(main#home) footer{--foreground-rgb: 255, 255, 255;--foreground-color: rgb(var(--foreground-rgb))}#home section:first-child{position:relative;top:0;left:0;width:100%;height:72vh}#home section:first-child .tagline{justify-content:center}#home section:first-child .row{position:absolute;bottom:var(--row-margin)}@media screen and (max-width: 767px)and (min-height: 1280px){#home section:first-child{height:56vh}}#home section:nth-child(2){position:sticky;z-index:-1;display:flex;align-items:center;top:0;right:0;bottom:0;left:0;width:100%;height:100vh}#home section:nth-child(2) video{position:absolute;left:50%;width:100vw;height:100%;transform:translateX(-50%);-o-object-fit:cover;object-fit:cover;-o-object-position:50% 50%;object-position:50% 50%;pointer-events:none;filter:brightness(72%)}#home section .tag{padding-left:calc(50% + var(--grid-gap)*.5)}@media screen and (max-width: 1023px){#home section .tag{padding-left:unset}}body:has(main#work) .fab.-center{display:none}#work section:first-child{display:flex;flex-direction:column;justify-content:flex-end;height:44vh}#work section:first-child .tagline{padding:unset}@media screen and (max-width: 767px)and (min-height: 1280px){#work section:first-child{height:32vh}}#work section:nth-child(2) .cell:nth-child(2){padding-left:calc(50% + var(--grid-gap)*.5)}@media screen and (max-width: 1023px){#work section:nth-child(2) .cell:nth-child(2){padding-left:unset}}#work section .row{background-image:unset}