.tagline{display:flex;flex-direction:column;align-items:flex-start;grid-column:1/-1;gap:.25em;padding:calc(var(--row-margin)*1.5) 0 var(--row-margin);overflow:hidden}.tagline div{display:flex;align-items:flex-start;overflow:hidden}.tagline div span{font-size:var(--display-size);font-weight:var(--title-weight);line-height:1;text-transform:uppercase;opacity:0}.tagline div span.-on{animation:taglineSlideUp 1s var(--ease) forwards}@keyframes taglineSlideUp{from{transform:translateY(150%);opacity:0}to{transform:translateY(0);opacity:1}}.tagline.-small div span{font-size:var(--title-size);font-weight:var(--title-weight)}