html{display:flex;background:var(--background-color)}body{display:flex;width:100%;color:var(--foreground-color);background:var(--background-color);font-size:var(--font-size)}#root{width:100%}main{z-index:1;display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);max-width:var(--body);padding:0 var(--margin);margin:0 auto}@media screen and (max-width: 1023px){main{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 767px){main{grid-template-columns:repeat(2, 1fr)}}#alert-tbd{position:fixed;z-index:9999;top:0;left:50%;padding:.5rem;margin-top:1rem;transform:translateX(-50%);background:red;font-size:var(--caption-size);font-weight:var(--caption-weight);line-height:var(--caption-height);letter-spacing:var(--caption-spacing);text-transform:uppercase}