.linktext{display:flex;align-items:center;overflow:hidden}.linktext:has(h3){gap:1rem}@media screen and (max-width: 767px){.linktext:has(h3){gap:.5rem}}.linktext:has(span){gap:.5rem}.linktext.-end{justify-content:flex-end}.linktext.-end .box{align-items:flex-end}.linktext.-dim{opacity:.4}.linktext .box{display:flex;flex-direction:column;overflow:hidden}.linktext .box:has(h3){gap:.5rem;height:calc(var(--headline-size)*var(--headline-height))}.linktext .box:has(span){height:calc(var(--caption-size)*var(--caption-height))}.linktext .box :first-child,.linktext .box :last-child{transform:none}.linktext .box :first-child{opacity:1}.linktext .box :last-child{opacity:0}.linktext h3,.linktext span{color:var(--foreground-color);transform-origin:50% 50% 0px;text-transform:uppercase;text-wrap:nowrap}.linktext h3{transition:all 1s var(--ease)}.linktext span{transition:all .6s var(--ease);font-size:var(--caption-size);font-weight:var(--caption-weight);line-height:var(--caption-height);letter-spacing:var(--caption-spacing)}.linktext a.box:has(h3):hover :first-child,.linktext a.box:has(h3):hover :last-child{transform:translate3d(0, calc(var(--headline-size) * var(--headline-height) * -1 - 0.5rem), 0)}.linktext a.box:has(span):hover :first-child,.linktext a.box:has(span):hover :last-child{transform:translate3d(0, calc(var(--caption-size) * var(--caption-height) * -1), 0)}.linktext a.box:hover :first-child{opacity:0}.linktext a.box:hover :last-child{opacity:1}.-hover .linktext .box:has(h3) :first-child,.-hover .linktext .box:has(h3) :last-child{transform:translate3d(0, calc(var(--headline-size) * var(--headline-height) * -1 - 0.5rem), 0)}.-hover .linktext .box:has(span) :first-child,.-hover .linktext .box:has(span) :last-child{transform:translate3d(0, calc(var(--caption-size) * var(--caption-height) * -1), 0)}.-hover .linktext .box :first-child{opacity:0}.-hover .linktext .box :last-child{opacity:1}